import * as React from "react";
import { useEffect } from "react";
import { useCookies } from "react-cookie";
import { useGlobal, State, Actions } from "../../state";
import AllActivitesWithMap from "./AllActivitiesWithMap";

export default (props) => {
  const [state, actions] = useGlobal<State, Actions>();
  const [cookies, setCookie, removeCookie] = useCookies(["auth"]);

  if (state.user === null) {
    return <div style={{ color: "white" }}>Sign in to view your activities</div>;
  }

  const handleClick = () => {
    fetch("/api/v1/strava/authorize", {
      method: "GET",
      headers: {
        Authorization: "Bearer " + cookies.apiKey,
      },
      redirect: "follow",
    }).then((resp) => {
      console.log("redirected");
    });
  };

  return (
    <div className="userActivities">
      {state.user.connectedToStrava ? (
        <div className="activitiesBody">
          <AllActivitesWithMap />
        </div>
      ) : (
        <div
          className="connectToStrava"
          style={{
            textAlign: "center",
            color: "white",
            fontSize: "2em",
            borderRadius: "5em",
            backgroundColor: "#fc5200",
            display: "flex",
            justifyContent: "center",
            marginLeft: "45vw",
            marginRight: "45vw",
          }}
          onClick={handleClick}
        >
          Connect to Strava
        </div>
      )}
    </div>
  );
};
